import axios from "axios";

const state = {
  posts: [],
  selectedPost: {},
};

const getters = {
  allPosts: (state) => state.posts,
  selected: (state) => state.selectedPost,
};

const actions = {
  fetchPosts({ commit }) {
    axios.get("/api/posts").then((resp) => {
      commit("set_posts", resp.data.posts);
    });
  },
  fetchPost({ commit }, post) {
    axios.get("/api/posts/" + post).then((resp) => {
      commit("selected_post", resp.data.post);
    });
  },
  upvotePost({ commit, rootState }, id) {
    axios.put("/api/posts/upvote/" + id).then((resp) => {
      const user = rootState.AuthStore.user.id;
      commit("upvote_post", { user, id });
      return resp;
    });
  },
  downvotePost({ commit, rootState }, id) {
    axios.put("/api/posts/downvote/" + id).then((resp) => {
      const user = rootState.AuthStore.user.id;
      commit("downvote_post", { user, id });
      return resp;
    });
  },
  deletePost({ commit }, id) {
    axios.delete("/api/posts/delete/" + id).then((resp) => {
      commit("delete_post", id);
      return resp;
    });
  },
  newPost({ commit }, post) {
    return new Promise((resolve, reject) => {
      axios
        .post("/api/posts", post, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((resp) => {
          commit("create_post", post);
          resolve(resp);
        })
        .catch((err) => {
          reject(err);
        });
    });
  },
  newComment({ commit, state }, content) {
    axios
      .post("/api/comments/new/" + state.selectedPost._id, {
        content,
      })
      .then((response) => {
        commit("add_comment", response.data.newComment);
      });
  },
  upComment({ commit, state, rootState }, commentId) {
    axios
      .put("/api/comments/upvote/" + state.selectedPost._id + "/" + commentId)
      .then(() => {
        const user = rootState.AuthStore.user.id;
        commit("upvote_comment", { user, commentId });
      });
  },
  downComment({ commit, state, rootState }, commentId) {
    axios
      .put("/api/comments/downvote/" + state.selectedPost._id + "/" + commentId)
      .then(() => {
        const user = rootState.AuthStore.user.id;
        commit("downvote_comment", { user, commentId });
      });
  },
  deleteComment({ commit, state }, id) {
    axios
      .delete("/api/comments/delete/" + state.selectedPost._id + "/" + id)
      .then(() => {
        commit("delete_comment", id);
      });
  },
};

const mutations = {
  set_posts(state, posts) {
    state.posts = posts;
    state.selectedPost = null;
  },
  selected_post(state, post) {
    state.selectedPost = post;
  },
  create_post(state, post) {
    state.posts.unshift(post);
  },
  delete_post(state, id) {
    state.posts = state.posts.filter((post) => post._id !== id);
  },
  upvote_post(state, { user, id }) {
    if (state.selectedPost) {
      state.selectedPost.voters.push(user);
    } else {
      const index = state.posts.findIndex((item) => item._id === id);
      state.posts[index].voters.push(user);
    }
  },
  downvote_post(state, { user, id }) {
    if (state.selectedPost) {
      const aux = state.selectedPost.voters.indexOf(user);
      if (aux > -1) {
        state.selectedPost.voters.splice(aux, 1);
      }
    } else {
      const index = state.posts.findIndex((item) => item._id === id);
      const aux = state.posts[index].voters.indexOf(user);
      if (aux > -1) {
        state.posts[index].voters.splice(aux, 1);
      }
    }
  },
  add_comment(state, comment) {
    state.selectedPost.comments.push(comment);
  },
  delete_comment(state, id) {
    state.selectedPost.comments = state.selectedPost.comments.filter(
      (comment) => comment._id !== id
    );
  },
  upvote_comment(state, { user, commentId }) {
    const index = state.selectedPost.comments.findIndex(
      (item) => item._id === commentId
    );
    state.selectedPost.comments[index].voters.push(user);
  },
  downvote_comment(state, { user, commentId }) {
    const index = state.selectedPost.comments.findIndex(
      (item) => item._id === commentId
    );
    const aux = state.selectedPost.comments[index].voters.indexOf(user);
    if (aux > -1) {
      state.selectedPost.comments[index].voters.splice(aux, 1);
    }
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
